import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../pages/Home'
import III from '../pages/III'
import VC from '../pages/VC'
import SA from '../pages/SA'
import SetUp from '../components/SetUp'
import Config from '../components/Config'

import IIISteps from '../data/III/install.json'
import IIIConfig from '../data/III/config.json'
import VCSteps from '../data/VC/install.json'
import VCConfig from '../data/VC/config.json'
import SASteps from '../data/SA/install.json'
import SAConfig from '../data/SA/config.json'

Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        component: Home,
    },
    {
        path: "/III",
        component: III,
        children: [
            {
                path: "",
                redirect: "Install"
            },
            {
                path: "Install",
                component: SetUp,
                props: {steps: IIISteps}
            },
            {
                path: "Configuration",
                component: Config,
                props: {steps: IIIConfig}
            }
        ]
    },
    {
        path: "/VC",
        component: VC,
        children: [
            {
                path: "",
                redirect: "Install"
            },
            {
                path: "Install",
                component: SetUp,
                props: {steps: VCSteps}
            },
            {
                path: "Configuration",
                component: Config,
                props: {steps: VCConfig}
            }
        ]
    },
    {
        path: "/SA",
        component: SA,
        children: [
            {
                path: "",
                redirect: "Install"
            },
            {
                path: "Install",
                component: SetUp,
                props: {steps: SASteps}
            },
            {
                path: "Configuration",
                component: Config,
                props: {steps: SAConfig}
            }
        ]
    }
]

const router = new VueRouter({
    mode: 'hash',
    base: "/rainbomizer/",
    routes,
    scrollBehavior() {
      document.getElementById('app').scrollIntoView()
    }
})

export default router